"use client";

import { useState, useEffect } from 'react';
import Image from 'next/image';
import { motion } from 'framer-motion';
import { Button } from "@/components/ui/button";
import { Card } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Calendar, Clock, MapPin, Users, Music, Mic, Book, Theater, Phone } from 'lucide-react';

const KavyanjaliPage = () => {
  const [isLoaded, setIsLoaded] = useState(false);

  useEffect(() => {
    setIsLoaded(true);
  }, []);

  const eventHighlights = [
    { icon: <Mic className="w-6 h-6" />, text: "Story Telling & Stand Up" },
    { icon: <Music className="w-6 h-6" />, text: "Singing and Music" },
    { icon: <Book className="w-6 h-6" />, text: "Book Stalls" },
    { icon: <Theater className="w-6 h-6" />, text: "Tuk Bandi" },
    { icon: <Mic className="w-6 h-6" />, text: "Open Mic" },
    { icon: <Theater className="w-6 h-6" />, text: "Drama" },
  ];

  return (
    <div className="min-h-screen bg-[url('/bg.png')] bg-cover bg-fixed p-4 sm:p-8 relative">
      <div className="absolute inset-0 bg-gradient-to-b from-orange-100/90 to-orange-200/90 backdrop-blur-sm" />
      
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ duration: 1 }}
        className="max-w-7xl mx-auto relative z-10"
      >
        {/* Decorative Elements */}
        <div className="absolute -top-10 -left-10 w-40 h-40 bg-orange-300/20 rounded-full blur-3xl animate-pulse" />
        <div className="absolute -bottom-10 -right-10 w-40 h-40 bg-red-300/20 rounded-full blur-3xl animate-pulse delay-700" />

        {/* Header Section */}
        <motion.div 
          className="text-center mb-16 relative"
          initial={{ y: -50 }}
          animate={{ y: 0 }}
          transition={{ duration: 0.8 }}
        >
          <div className="relative inline-block">
            <motion.div
              initial={{ scale: 0 }}
              animate={{ scale: 1 }}
              transition={{ delay: 0.2, type: "spring" }}
              className="absolute -top-8 -left-8 w-16 h-16"
            >
              <Image
                src="/assets/images/last.png"
                alt="Decoration"
                width={100}
                height={100}
                className="animate-float"
              />
            </motion.div>
            
            <h1 className="font-samarka text-6xl sm:text-8xl text-orange-900 mb-4 relative">
              <span className="absolute -top-1 -left-1 text-orange-300 opacity-50 blur-[1px]">काव्यमंच</span>
              काव्यमंच
              <motion.span 
                className="block text-3xl sm:text-4xl text-orange-700 mt-2"
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                transition={{ delay: 0.5 }}
              >
                Kavyamanch
              </motion.span>
            </h1>
          </div>

          <div className="flex flex-wrap justify-center items-center gap-4 mt-8">
            <Badge variant="outline" className="px-4 py-2 text-lg bg-white/50 backdrop-blur-sm">
              <Calendar className="w-4 h-4 mr-2" /> 20th February
            </Badge>
            <Badge variant="outline" className="px-4 py-2 text-lg bg-white/50 backdrop-blur-sm">
              <Clock className="w-4 h-4 mr-2" /> 2PM - 5PM
            </Badge>
            <Badge variant="outline" className="px-4 py-2 text-lg bg-white/50 backdrop-blur-sm">
              <MapPin className="w-4 h-4 mr-2" /> Open Air Auditorium
            </Badge>
          </div>
        </motion.div>

        {/* Main Content Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
          {/* Left Column - Event Details */}
          <motion.div
            initial={{ x: -100, opacity: 0 }}
            animate={{ x: 0, opacity: 1 }}
            transition={{ delay: 0.3 }}
          >
            <Card className="p-6 backdrop-blur-md bg-white/80">
              <h3 className="font-samarka text-4xl text-orange-800 mb-8 text-center">
                Event Highlights
              </h3>
              <div className="grid grid-cols-1 sm:grid-cols-2 gap-6">
                {eventHighlights.map((item, index) => (
                  <motion.div
                    key={index}
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ delay: 0.1 * index }}
                    className="flex items-center gap-4 p-4 rounded-lg bg-orange-50/50 hover:bg-orange-50 transition-all duration-300 group"
                  >
                    <div className="text-orange-500 transition-all duration-300 group-hover:scale-110">
                      {item.icon}
                    </div>
                    <p className="text-lg text-orange-900 font-medium">{item.text}</p>
                  </motion.div>
                ))}
              </div>
            </Card>
          </motion.div>

          {/* Right Column - Registration Form */}
          <motion.div
            initial={{ x: 100, opacity: 0 }}
            animate={{ x: 0, opacity: 1 }}
            transition={{ delay: 0.3 }}
          >
            <Card className="p-6 backdrop-blur-md bg-white/80">
              <h3 className="font-samarka text-4xl text-orange-800 mb-8 text-center">
                Registration
              </h3>
              <div className="w-full min-h-[500px] bg-orange-50/50 rounded-lg p-4 relative overflow-hidden">
                {/* Google Form will go here */}
                <div className="text-center text-orange-800 font-samarka text-xl">
                  Registration Form Loading...
                </div>
              </div>
            </Card>
          </motion.div>
        </div>

        {/* Contact Section */}
        <motion.div
          initial={{ y: 50, opacity: 0 }}
          animate={{ y: 0, opacity: 1 }}
          transition={{ delay: 0.8 }}
          className="mt-12 text-center"
        >
          <Card className="p-6 backdrop-blur-md bg-white/80 max-w-2xl mx-auto">
            <h3 className="font-samarka text-3xl text-orange-800 mb-6 flex items-center justify-center gap-2">
              <Users className="w-6 h-6" /> Student Co-ordinators
            </h3>
            <div className="flex flex-wrap justify-center gap-8">
              <Button variant="outline" className="text-lg p-6 hover:bg-orange-100">
                <Phone className="w-4 h-4 mr-2" />
                Dharmbir Singh: 8887808716
              </Button>
              <Button variant="outline" className="text-lg p-6 hover:bg-orange-100">
                <Phone className="w-4 h-4 mr-2" />
                Dheeraj Saraswat: 6367510083
              </Button>
            </div>
          </Card>
        </motion.div>
      </motion.div>
    </div>
  );
};

export default KavyanjaliPage; 